import React from 'react';
import '../css/Header.css';
import { Link, Redirect      }  from 'react-router-dom'
import { setPathUrl } from '../actions'; 
import { connect             }  from "react-redux";




const mapStateToProps = state => {
    return {  
        lastPathUrl               : state.lastPathUrl,

          };
  };

  
function mapDispatchToProps(dispatch) {
    return {
        setPathUrl : bol    => dispatch(setPathUrl(bol))
          };
  }




class connectedHeader extends React.Component {
    constructor(props) {
        super(props)
    
        this.state = {
            lastPathUrl: ''
        }
    }

    componentDidUpdate(prevProps) {
      
    }

    selectCar(e) {
        let value = e.target.innerHTML;
        this.props.setPathUrl({ lastPathUrl: value })
        this.setState({lastPathUrl: value })
        
        
    }

    render() {
        return (
                <div>
                    <div className='header_container'>
                        <div className='head_cont_wraplogo'></div>

                        <div className='head_cont_wrapnav'>
                            <a href='https://www.google.ro'>Home</a>
                            <a href='https://www.google.ro'>Products</a>
                            <a href='https://www.google.ro'>About</a>
                            <a href='https://www.google.ro'>Story</a>
                        </div>
                    </div>
                </div>
        )
    }
}

const Header = connect(mapStateToProps,mapDispatchToProps)(connectedHeader);
export default Header;
